import axios from 'axios';
import React, {useEffect, useState} from 'react';
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import GithubCorner from 'react-github-corner';
import {BrowserRouter as Router, Route} from 'react-router-dom';
import './App.scss';
import {AppConfig} from './AppConfig';
import Calculator from './components/calculator/Calculator';

/**
 * Loads the JSON configuration file and renders the application.
 */
export default function App() {
  const configUrl = process.env.PUBLIC_URL + '/config.json';
  const [progress, setProgress] = useState<string | null>('Loading configuration...');
  const [config, setConfig] = useState({} as AppConfig);

  const hasSelection = () => {
    if (window.getSelection) {
      const selection = window.getSelection();
      return selection && selection.toString() !== '';
    }
    // Unsupported browser
    return true;
  };

  const getTooltip = () => {
    const tooltip = document.querySelector('[role="tooltip"] .tooltip-inner');
    if (tooltip && tooltip instanceof HTMLElement && tooltip.innerHTML) {
      return tooltip.innerHTML;
    }
    return null;
  };

  const setClipboard = (e: ClipboardEvent, tooltip: Element) => {
    if (!e.clipboardData) {
      return;
    }
    if (tooltip instanceof HTMLElement && tooltip.innerHTML) {
      console.log('text/html');
      e.clipboardData.setData('text/html', tooltip.innerHTML);
    }
    if (tooltip && tooltip.textContent) {
      e.clipboardData.setData('text/plain', tooltip.textContent);
    }
    e.preventDefault();
  };

  const onCopy = (e: Event) => {
    console.log('COPY', e instanceof ClipboardEvent, e);
    if (e instanceof ClipboardEvent && e.clipboardData) {
      console.log('COPY');
      const tooltip = document.querySelector('[role="tooltip"] .tooltip-inner');
      if (tooltip) {
        setClipboard(e, tooltip);
        return;
      }

      if (hasSelection()) {
        return;
      }

      // const table = document.querySelector('[role="table"]');
      // if (table) {
      //   const groups = table.querySelectorAll('[role="rowgroup"]');
      //
      //   const header = Array.from(groups[0].querySelectorAll('[role="columnheader"]')).map(
      //     (header) => header.textContent
      //   );
      //
      //   const rows = Array.from(groups[1].querySelectorAll('[role="row"]')).map((row) => {
      //     return Array.from(row.querySelectorAll('[role="cell"]'))
      //       .map((cell) => cell.textContent)
      //       .join(' | ');
      //   });
      //
      //   const md =
      //     header.join(' | ') + '\n' + header.map((_) => ':-:').join(' | ') + '\n' + rows.join('\n');
      //   console.log(md);

      const table = document.querySelector('[role="table"]');
      if (table) {
        const groups = table.querySelectorAll('[role="rowgroup"]');

        const header =
          '<thead><tr>' +
          Array.from(groups[0].querySelectorAll('[role="columnheader"]'))
            .map((header) => `<th align="center">${header.textContent}</th>`)
            .join('') +
          '</thead></tr>';

        const rows =
          '<tbody>' +
          Array.from(groups[1].querySelectorAll('[role="row"]'))
            .map((row) => {
              return (
                '<tr>' +
                Array.from(row.querySelectorAll('[role="cell"]'))
                  .map((cell) => `<td>${cell.textContent}</td>`)
                  .join('') +
                '</tr>'
              );
            })
            .join('\n') +
          '</tbody>';

        const html = ('<table>' + header + '\n' + rows + '</table>')
          // Using `<br>` works in, e.g., Discourse that converts HTML to
          // Markdown. It won't look nice in, e.g., Word.
          .replace(/\u200B\u200B/g, '&lt;br/>')
          .replace(/\u200B/g, ' ');

        console.log(html);
        e.clipboardData.setData('text/html', html);
        e.preventDefault();
      }
    }
  };

  // Given the empty array for deps, this only runs when component mounts;
  // https://reactjs.org/docs/hooks-reference.html#conditionally-firing-an-effect
  useEffect(() => {
    console.error('Init keydown');
    // window.addEventListener('keydown', handleKeyDown);
    window.addEventListener('copy', onCopy);

    // cleanup this component
    return () => {
      console.error('Remove keydown');
      // window.removeEventListener('keydown', handleKeyDown);
      window.removeEventListener('copy', onCopy);
    };
  }, []);

  useEffect(() => {
    let didCancel = false;
    const fetchConfig = async () => {
      try {
        // By default, axios expects JSON in UTF8, and converts that on the fly
        const result = (await axios.get<AppConfig>(configUrl)).data;
        if (!didCancel) {
          setProgress(null);
          setConfig(result);
        }
      } catch (error) {
        // TODO fine for 404, but this does not catch JSON parse errors?
        setProgress(
          `Failed to load configuration ${process.env.PUBLIC_URL} ${configUrl}: ${error}`
        );
      }
    };

    // Workaround to suppress the following React warning:
    //   Warning: useEffect function must return a cleanup function or nothing.
    //   Promises and useEffect(async () => ...) are not supported, but you can
    //   call an async function inside an effect.
    // This still makes the compiler warn about ignoring the returned Promise.
    fetchConfig();
    return () => {
      didCancel = true;
    };
  }, [configUrl]);

  return (
    <Router basename={process.env.PUBLIC_URL}>
      <Container fluid className="App">
        <Row>
          <Col>
            <h1>Airtime calculator for LoRaWAN</h1>
            {/*TODO check if IE does not show text null */}
            <p>{progress}</p>
          </Col>
        </Row>
        {config.networks && <Route render={(props) => <Calculator {...props} config={config} />} />}
        <GithubCorner href="https://github.com/avbentem/airtime-calculator" />
      </Container>
    </Router>
  );
}
